<template>
  <div class="container">
    <Sidebar active="query" />
    <main>
      <h1>QUERY AN ENDPOINT</h1>
      <section>
        <div class="query">
          <div class="url">http://{{ url }}</div>
          <input placeholder="endpoint" autofocus type="text" v-model="route" />
          <button @click="send">Send</button>
        </div>
        <div class="query">
          <code>
            {{response}}
          </code>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
import Sidebar from "../components/Sidebar.vue";
export default {
  name: "Dashboard",
  components: {
    Sidebar,
  },
  data() {
    return {
      response: "",
      route: "",
    };
  },
  mounted() {
    if (this.$route.params.route == null) return;
    this.route = this.$route.params.route;
  },
  computed: {
    token() {
      return this.$store.state.token;
    },
    url() {
      return `${location.host}/${this.token}/`;
    },
  },
  methods: {
    send() {
      const data = this.$store.getters.info(this.route);
      this.response = JSON.parse(JSON.stringify(data));
    },
  },
};
</script>

<style lang="scss" scoped>
