  display: flex;
}
main {
  width: 100%;
  h1 {
    padding: 30px 0 0 30px;
    text-align: left;
    font-weight: 600;
    height: 10%;
  }
}
section {
  height: 90%;
  padding: 30px;
  overflow-x: hidden;
  overflow-y: auto;
}
.query,
#query {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px;
  .url {
    background: #202020;
    border-radius: 10px 0 0 10px;
    height: 60px;
    color: #59c3ff;
    letter-spacing: 3px;
    font-size: 1.3rem;
    line-height: 60px;
    padding: 0 10px 0 50px;
  }
  input {
    background: #2e2e2e;
    width: 150px;
    border-radius: 0 10px 10px 0;
    height: 60px;
    text-align: center;
    border: none;
    outline: none;
    color: #59ffb2;
    font-family: inherit;
    letter-spacing: 3px;
    font-size: 1.3rem;
  }
  button {
    color: #06ff88;
    cursor: pointer;
    background: #202020;
    border-radius: 10px;
    text-align: center;
    border: none;
    outline: none;
    height: 60px;
    font-size: 1.25rem;
    width: 150px;
    margin-left: 30px;
  }
}
#query {
  text-align: left;
}
code {
  padding: 30px;
  height: 300px;
  width: 50%;
  background: #202020;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  text-align: left;
}
pre {
  background: transparent !important;
  text-align: left;
}
</style>